

import BookMarkItems from './CartItems';


const BookMark =()=>{
  const getItem  = JSON.parse(localStorage.getItem('book'))
  
console.log(getItem)


const CartItem = getItem.map(cart => (
  <BookMarkItems
    key={cart.id}
    id={cart.id}
    Name={cart.Name}
    Price={cart.price}
    Image={cart.thumbnail}
    Amount={cart.rating.average}
    />))
    
  
  return (
      <div>  
     {getItem.length === 0 &&!getItem && <p>No Bookmarks</p>}
        {!CartItem && <p>LoadCartItems</p>} 
        {CartItem}
   </div>

    )
};

export default BookMark;