import classes from './Products.module.css'
import { Fragment} from 'react';
import { Link, NavLink } from 'react-router-dom';
// import Info from '../Information/info';
import {  AiOutlineShoppingCart} from "@react-icons/all-files/ai/AiOutlineShoppingCart";
import { BsBookmarksFill ,BsBookmarks} from "react-icons/bs";
import { useMediaQuery } from 'react-responsive';
import {  useCart } from "react-use-cart"
import {addBookmark,removeBookmark} from '../store/bookmark-hook';
import { Button } from 'react-bootstrap';



const Products =(props)=>{
const {addItem,items} = useCart();

const addBookmarks = ()=>{
	
	addBookmark(props)
}


	if ( useMediaQuery({ query: '(max-width: 540px)' })) {
		const Items= (items.map((item)=>item.id === props.id)) 
		const True = Items.filter((item)=>item === true)
		const getItem  = JSON.parse(localStorage.getItem('book'))
		const BookMarkitems= ((getItem||[]).map((item)=>item.id === props.id)) 
		const True2 = BookMarkitems.filter((item)=>item === true)
		
		return(
		<Fragment>
        <div className={classes.products}>
		{' '}

        <img src={props.thumbnail}  alt="Avatar" style={{'width':'100%', 'marginTop':'10px','borderRadius':'30px'}} />
        <div className={classes.container}>
		<p className={classes.PG}> imbd-{props.rating}</p>
		{!(True2[0] || undefined)&&
	    <Button    onClick={addBookmarks}>
     	<BsBookmarks />
        </Button> 
       }
	
        {(True2[0] || undefined)&&
	    <Button    onClick={()=>removeBookmark(props)}>
        <BsBookmarksFill />
        </Button> 
	   }
        <h4>	<NavLink to={`/Shopping-Cart/Info/${props.id}`} > View More </NavLink></h4>
		{!(True[0] || undefined)&&<button onClick={()=>addItem(props)}>
		<AiOutlineShoppingCart /> ADD TO CART</button>}
        {(True[0] || undefined)&&
	    <button> <Link to='/Shopping-Cart/CartItems'><span>DashBoard</span></Link></button>}
        </div>
        </div>
        </Fragment>
	)
}

       const Items= (items.map((item)=>item.id === props.id)) 
       const True = Items.filter((item)=>item === true)
       const getItem  = JSON.parse(localStorage.getItem('book'))
       const BookMarkitems= ((getItem||[]).map((item)=>item.id === props.id)) 
       const True2 = BookMarkitems.filter((item)=>item === true)
    // const False2 = BookMarkitems.filter((item)=>item === false)


     return (
        <Fragment>
        <div className={classes.wrapper}>
	    <div className={classes.main_card}>
		<div className={classes.card_left}>
		<div className={classes.card_datails}>	  
	    {/* {(True2[0] || undefined)&&<BsBookmarksFill onClick={props.BookMark2}/>}  */}
		{!(True2[0] || undefined)&&
	    <Button    onClick={addBookmarks}>
     	<BsBookmarks />
        </Button> 
       }
	
        {(True2[0] || undefined)&&
	    <Button    onClick={()=>removeBookmark(props)}>
        <BsBookmarksFill />
        </Button> 
	   }
		<h1>{props.Name}</h1>
		<div className={classes.card_cat}>
		<p className={classes.PG}> imbd-{props.rating}</p>
		<p className={classes.year}>{props.airdate}</p>
		<p className={classes.genre}>{ props.genres[0]}|{ props.genres[1]}|{props.genres[2]}</p>
		<p className={classes.time}>{props.runtime}m</p>
		</div>
		{/* <p className={classes.disc}>{props.summary[0]}.</p> */}
		<NavLink to={`/Shopping-Cart/Info/${props.id}`} > View More </NavLink>
		<div className={classes.btn}>
       
        <button>
        <i className={classes.fas}><a href={props.url} target="_blank" rel="noreferrer">See Trailer</a></i>
        </button>
		
				<button>
				<i className={classes.fas}><a href={props.url} target="_blank" rel="noreferrer">Downlaod</a></i>
				</button>
			    {!(True[0] || undefined)&&<button onClick={()=>addItem(props)} >
				 <AiOutlineShoppingCart /> ADD TO CART</button>}
                {(True[0] || undefined)&&
			    <button> <Link to='/Shopping-Cart/CartItems'><span>DashBoard</span></Link></button>}
			    </div>	
			    </div>
   
		        </div>
	     	    <div className={classes.card_right}>
			    <div className={classes.img_container}>
				<img src={props.thumbnail}  alt=""/>
				</div>
				<div className={classes.play_btn}>
				<NavLink to="https://www.imdb.com/title/tt4912910/" target="_blank">
				<i className={classes.play}></i>
				</NavLink>
				</div>
			</div>
		</div>
	</div>


      </Fragment>
    )
};

export default Products;